<template>
  <div class="app">
    <v-app>
      <v-main>
        <v-container class="grey lighten-4" fluid fill-height>
          <v-col>
            <v-row justify="center">
              <p class="text-h3 font-weight-light login-text">Autotracks</p>
            </v-row>
            <v-row class="mb-3" justify="center">
              <p class="font-weight-light login-text">
                Login in to your favorite management console
              </p>
            </v-row>
            <v-alert
              v-if="errorMessage"
              color="error"
              outlined
              dense
              text
              min-width="500"
              max-width="500"
              class="mx-auto"
              >{{ errorMessage }}</v-alert
            >
            <v-card min-width="500" max-width="500" class="mx-auto pa-2">
              <form @submit.prevent="login">
                <v-card-text>
                  <v-text-field label="Email" outlined v-model="email" dense />
                  <v-text-field
                    label="Password"
                    type="password"
                    outlined
                    v-model="password"
                    hide-details
                    dense
                  />
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                  <v-btn color="primary" rounded type="submit"> Login </v-btn>
                  <v-spacer />
                  <p class="text-caption mb-0">
                    Don't have an account?
                    <router-link to="register">Register here</router-link>
                  </p>
                </v-card-actions>
              </form>
            </v-card>
          </v-col>
        </v-container>
      </v-main>
    </v-app>
  </div>
</template>

<script>
export default {
  name: "Login",

  data: () => ({
    email: "",
    password: "",
    errorMessage: null,
  }),
  methods: {
    async login() {
      const payload = {
        email: this.email,
        password: this.password,
      };

      const res = await this.$store.dispatch("login", payload);
      if (res.success) {
        this.$router.push("/dashboard");
      } else {
        this.showError(res.message);
      }
    },
    showError(message) {
      this.errorMessage = message;
      setTimeout(() => {
        this.errorMessage = null;
      }, 4000);
    },
  },
};
</script>

<style>
.login-text {
  display: inline-block;
}
</style>